<template>
  <div class="container-fluid py-4">
    <div class="row mb-4">
      <div class="col-12">
        <h2>
          <i class="fas fa-comments me-2"></i>
          Chat Management
        </h2>
        <p class="text-muted">Respond to subscriber messages as your model</p>
      </div>
    </div>

    <div class="card">
      <div class="card-header">
        <h5 class="mb-0">Subscriber Chats</h5>
      </div>
      <div class="card-body">
        <div v-if="loading" class="text-center py-3">
          <div class="spinner-border" role="status">
            <span class="visually-hidden">Loading...</span>
          </div>
        </div>
        
        <div v-else-if="subscribers.length === 0" class="text-center py-5">
          <i class="fas fa-inbox fa-3x text-muted mb-3"></i>
          <h4 class="text-muted">No subscribers yet</h4>
          <p class="text-muted">When users subscribe to your model, their chats will appear here.</p>
        </div>
        
        <div v-else>
          <div
            v-for="subscriber in subscribers"
            :key="subscriber.id"
            class="d-flex align-items-center mb-3 p-3 border rounded"
          >
            <div class="avatar me-3" style="width: 50px; height: 50px; background-color: #6f42c1; color: white; display: flex; align-items: center; justify-content: center; border-radius: 50%;">
              {{ subscriber.user.email.charAt(0).toUpperCase() }}
            </div>
            <div class="flex-grow-1">
              <h6 class="mb-1">{{ subscriber.user.email }}</h6>
              <small class="text-muted">
                Subscribed {{ formatDate(subscriber.createdAt) }}
              </small>
            </div>
            <button class="btn btn-primary btn-sm">
              <i class="fas fa-comment me-1"></i>
              Open Chat
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from '../../services/api'

export default {
  name: 'ManagerChats',
  data() {
    return {
      subscribers: [],
      loading: true
    }
  },
  async mounted() {
    await this.fetchSubscribers()
  },
  methods: {
    async fetchSubscribers() {
      try {
        this.loading = true
        const response = await api.get('/manager/chats')
        this.subscribers = response.data.subscribers
      } catch (error) {
        console.error('Error fetching subscribers:', error)
      } finally {
        this.loading = false
      }
    },

    formatDate(timestamp) {
      const date = new Date(timestamp)
      const now = new Date()
      const diff = now - date

      if (diff < 86400000) { // Less than 1 day
        return 'today'
      } else if (diff < 172800000) { // Less than 2 days
        return 'yesterday'
      } else {
        return date.toLocaleDateString()
      }
    }
  }
}
</script>
